.List.BicyclesList {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.BicycleCard {
    display: flex;
    flex-direction: column;
    margin-bottom: 18px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 4px 4px 14px 2px rgba(0, 0, 0, 0.12);
}

.BicycleCard__Photo {
    display: flex;
    justify-content: center;
}

.BicycleCard__Photo img {
    max-width: 100%;
    width: 320px;
    height: 220px;
}

.BicycleCard__Name {
    color: rgba(0, 0, 0, 0.625);
}

.BicycleCard__Footer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
}

.BicycleCard__Footer .Price {
    flex-basis: 40%;
}

.BicycleCard__Footer .BuyBtn {
    flex-basis: 60%;
}

.Price__Value {
    margin: 0;
    font-size: 26px;
    font-weight: 600;
}

.BuyBtn--InBasket .Button {
    background-color: var(--accent);
    color: var(--white);
}
.Group.FiltersGroup {
    margin-top: 0;
    margin-bottom: 0;
}

.Group.FiltersGroup::after {
    content: none;
}

.Div.FiltersList {
    display: flex;
    padding-top: 0;
}

.FiltersList--Price p {
    margin: 0;
}

.FiltersList__Item {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px 4px;
    margin-right: 4px;
    border-radius: 4px;
    border: 1px solid var(--field_border);
    font-size: 15px;
}

.FiltersList__Item span {
    margin-right: 4px;
    color: var(--attach_picker_tab_inactive_text);
}

.FiltersList__Item svg,
.FiltersList__Item .Icon {
    width: 12px!important;
    height: 12px !important;
}
.PriceRangeSlider {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
}
.BikePage {
  background-color: var(--white);
  padding-bottom: 60px;
}

.FixedBuyBtn {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)), color-stop(5%, rgba(255,255,255,1)));
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(255,255,255,1) 5%);
}

.FixedBuyBtn--InBasket > .Button {
  background-color: var(--accent);
  color: var(--white);
}

.Gallery {
  height: auto!important;
}
.BasketBike .Avatar__img{
  width:70px!important;
  height: 50px!important;
  border-radius: 0!important;
}

.BasketBike__Price {
  display: block;
  margin-top: 12px;
  font-size: 18px;
}

.BasketSum__Price {
  display: block;
  margin-top: 12px;
  margin-bottom: 12px;
  font-size: 30px;
  font-weight: bold;
  color: var(--accent);
}

.BasketSum__ItemSum {
  font-weight: bold;
  color: var(--button_muted_background);
}

.BasketSum .Cell__main {
  padding-right: 0;
}

.UserInfo .Button {
  padding-top: 0;
  padding-left: 0;
  border-radius: 0;
}
.UserInfo .Button__content {
  padding-top: 0;
}
.PopoutWrapper__container {
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
    background-color: rgba(40, 43, 46, 0.2);
}

.PopoutWrapper {
    height: 100vh;
}

.Button--disabled {
    opacity: .5;
    pointer-events: none;
}

#root {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.old_ios #root {
    -webkit-overflow-scrolling: auto;
}

.Icon--28,
.Icon--28 svg {
    width: 28px!important;
    height: 28px!important;
}
